import { getMenuContent } from '../../../services/menu-items-service';
import { navContentStore } from '../../../state/nav-content-store';
import { h } from '@stencil/core';
import { isDesktop } from '../../../utility/helper';
import { TVehiclePurchaseMenu } from '../../../entities/content';

type VehiclePurchaseSectionTypes = {
  activeRoutingKey: string;
  topLevelRoutingKey: string;
  breakpoint: number;
  receivedContent: boolean;
};

export default function VehiclePurchaseSection({
  activeRoutingKey,
  topLevelRoutingKey,
  breakpoint,
  receivedContent
}: VehiclePurchaseSectionTypes): HTMLElement {
  const showHeadline = !isDesktop(breakpoint);
  const displayedContent = getMenuContent(
    navContentStore.state,
    activeRoutingKey,
    topLevelRoutingKey
  ) as TVehiclePurchaseMenu;

  /* istanbul ignore if */
  if (displayedContent === null) {
    return <div></div>;
  }

  return (
    <phn-menu
      item={displayedContent}
      routingKey={activeRoutingKey}
      showHeadline={showHeadline}
      receivedContent={receivedContent}
    />
  );
}
